.header {
  padding: 12px 0px;
  background: white;
  border-bottom: thin solid #e1e1e;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1000; }
  .header .logo img {
    height: 35px; }
  .header .search {
    margin-left: 30px;
    position: relative; }
    .header .search-group {
      background: #efeff4;
      border-radius: 100px; }
      .header .search-group input {
        -webkit-transition: .3s ease-in-out;
        transition: .3s ease-in-out;
        min-width: 300px;
        padding: 6px 20px;
        background: none;
        border: none;
        height: auto;
        border-radius: 100px; }
        .header .search-group input:focus {
          outline: none;
          background: white;
          box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); }
    .header .search i {
      position: absolute;
      top: 50%;
      -webkit-transform: translate(-100%, -50%);
              transform: translate(-100%, -50%);
      right: 0;
      color: #333;
      cursor: pointer; }
  .header .action {
    margin-left: auto; }
    .header .action ul {
      list-style: none;
      margin: 0;
      padding: 0; }
      .header .action ul li {
        border-radius: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 4px 15px;
        color: white;
        cursor: pointer;
        -webkit-transition: .3s ease-in-out;
        transition: .3s ease-in-out;
        white-space: nowrap; }
        .header .action ul li:hover {
          box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2); }
    .header .action-register {
      background: #1f9ffc; }
      .header .action-register a {
        text-decoration: none;
        color: white; }
    .header .action-login {
      background: #ff9e6a; }
      .header .action-login a {
        text-decoration: none;
        color: white; }
  .header .info {
    position: relative; }

.slide {
  width: 220px;
  position: -webkit-sticky;
  position: sticky;
  top: 60px;
  display: inline-block;
  box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.05);
  border-radius: 0px;
  background: white;
  height: calc( 100vh - 60px);
  padding: 1rem 0; }
  .slide .slide-bar {
    overflow: hidden;
    width: 100%; }
    .slide .slide-bar ul li {
      margin-bottom: 20px; }
      .slide .slide-bar ul li a {
        display: flex;
        align-items: center;
        text-decoration: none;
        color: #aaa; }
        .slide .slide-bar ul li a svg {
          height: 20px;
          margin-right: 12px;
          fill: #aaa; }
      .slide .slide-bar ul li:hover a {
        color: #333; }
        .slide .slide-bar ul li:hover a svg {
          fill: #333; }
      .slide .slide-bar ul li a.active {
        color: #333;
        font-weight: bold; }
        .slide .slide-bar ul li a.active svg {
          fill: #333; }
  .slide .info {
    padding: 1rem 2rem; }
    .slide .info-item {
      display: flex;
      align-items: center;
      color: #aaa;
      margin-bottom: 16px; }
      .slide .info-item svg {
        height: 20px;
        fill: #aaa;
        margin-right: 12px; }
  .slide .author {
    padding: 1rem 2rem; }
  .slide .more {
    padding: 1rem 2rem;
    background: #efeff48c; }
  .slide .slide-main {
    padding: 1rem 2rem; }

.App footer {
  position: fixed;
  display: flex;
  justify-content: center;
  bottom: 0;
  left: 0;
  background: white;
  width: 100%;
  box-shadow: 0px -10px 24px rgba(0, 0, 0, 0.2); }
  .App footer .tab {
    padding: 14px 18px;
    font-weight: 600;
    color: #aaa;
    text-decoration: none; }
    .App footer .tab svg {
      height: 20px;
      fill: #aaa;
      margin-right: 5px; }
    .App footer .tab span {
      white-space: nowrap; }
  .App footer .tab.active {
    color: #333;
    text-decoration: none; }
    .App footer .tab.active:active {
      text-decoration: none; }
    .App footer .tab.active * {
      text-decoration: none; }
    .App footer .tab.active svg {
      height: 20px;
      fill: #000; }

@media screen and (max-width: 400px) {
  footer .tab {
    padding: 14px 12px; }
    footer .tab span {
      font-size: .9rem; } }

@media screen and (min-width: 1024px) {
  .App footer {
    display: none; } }

